<template>
  <div class="home-container">
    <!-- 头部区域 -->
    <van-nav-bar title="头条" fixed />

    <van-pull-refresh v-model="isLoading" @refresh="onRefresh" :disabled="finished">
      <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
        <artcleInfo v-for="item in artlist" :key="item.art_id" :title="item.title" :author="item.aut_name" :commCount="item.comm_count" :pubDate="item.pubdate" :cover="item.cover"></artcleInfo>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import { getArtcleListAPI } from '@/api/artcleApi'
import artcleInfo from '@/components/Artcle/artcleInfo.vue'

export default {
  name: 'Home',
  components: {
    artcleInfo
  },
  data() {
    return {
      page: 1,
      limit: 10,
      artlist: [],
      // 是否正在加载下一页数据
      // 如果 loading 为 true， 则不会反复触发 load 事件
      loading: true,
      // 是否加载完所有数据
      // 如果没有更多数据，则变为true
      finished: false,
      // 是否正在下拉刷新
      isLoading: false
    }
  },
  methods: {
    // 封装获取文章列表数据的方法
    async initArtcleList(isRefresh) {
      const { data: res } = await getArtcleListAPI(this.page, this.limit)
      if (isRefresh) {
        // 证明是下拉刷新
        this.artlist = [...res, ...this.artlist]
        this.isLoading = false
      } else {
        // 证明是上拉加载更多
        this.artlist = [...this.artlist, ...res]
        this.loading = false
      }
      // console.log(this.artlist.length)
      if (res.length === 0) {
        this.finished = true
      }
    },
    onLoad() {
      // 页码值 +1
      // 重新请求接口获取数据
      this.page++
      this.initArtcleList(false)
    },
    // 下拉刷新的处理函数
    onRefresh() {
      // console.log('触发下拉刷新')
      // 让页码值 +1
      this.page++
      // 重新请求接口获取数据
      this.initArtcleList(true)
    }
  },
  created() {
    this.initArtcleList()
  }
}
</script>

<style lang="less" scoped>
.home-container {
  padding: 46px 0 50px 0;
}
</style>
